<template>
  <div class="container max-w-4xl my-10">
    <div class="bg-white rounded px-1 pt-6 pb-8 mb-4">
      <h1 class="text-xl text-center pb-3">{{ title }}</h1>
      <div class="flex-col">
        <div class="flex flex-row">
          <div class="w-5/6">
            <video
              id="video"
              ref="videoPlayer"
              @timeupdate="onTimeUpdate($event)"
              @loadedmetadata="onLoadedMetadata($event)"
              crossorigin="anonymous"
            ></video>
          </div>
          <div
            class="flex flex-no-wrap flex-col w-1/6 ml-3 border-gray-200 border-1"
          >
            <div
              v-for="(k, index) in keyEvents"
              class="moment"
              :key="index"
              v-on:click="player.currentTime = k.videoTime"
            >
              <span class="inline-block">{{ k.friendly_name }}</span>
              <img
                class="inline-block"
                v-if="k.teamLogo"
                :src="k.teamLogo"
                width="20"
              />
            </div>
          </div>
        </div>
        <div>
          <canvas class="hidden" id="canvas"></canvas>
        </div>
        <div class="text-xs mt-3 whitespace-pre">
          <ul class="flex">
            <li class="flex-1 mr-2">
              <a
                class="text-center block border border-blue-500 rounded py-2 px-4 bg-blue-500 hover:bg-blue-700 text-white"
                href="#"
                v-on:click="
                  ActiveCalibrate = true;
                  ActiveWebVTT = false;
                  calibrate();
                "
                >Calibrate</a
              >
            </li>
            <li class="flex-1 mr-2">
              <a
                class="text-center block border border-blue-500 rounded py-2 px-4 bg-blue-500 hover:bg-blue-700 text-white"
                href="#"
                v-on:click="
                  ActiveCalibrate = false;
                  ActiveWebVTT = true;
                "
                >WebVTT</a
              >
            </li>
          </ul>
        </div>
        <div
          v-if="ActiveWebVTT"
          class="overflow-x-scroll text-xs mt-3 whitespace-pre"
        >
          <p>{{ webvtt }}</p>
        </div>
        <div v-if="ActiveCalibrate" class="text-xs mt-3 whitespace-pre">
          <table class="table-auto object-center">
            <thead>
              <tr>
                <th class="px-4 py-2">Sample</th>

                <th class="px-4 py-2">Match Time (s)</th>
                <th class="px-4 py-2">Video Time (s)</th>
                <th class="px-4 py-2">Match Time</th>
                <th class="px-4 py-2">Video Time</th>
                <th class="px-4 py-2">Offset</th>
                <th class="px-4 py-2">Half</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(data, i) in calbrateData"
                v-bind:key="i"
                v-on:click="player.currentTime = data.time"
              >
                <td class="border px-4 py-2">{{ i }}</td>

                <td class="border px-4 py-2">{{ data.matchtime }}</td>
                <td class="border px-4 py-2">{{ data.time }}</td>
                <td class="border px-4 py-2">
                  {{
                    `${Math.floor(data.matchtime / 60)}:${data.matchtime % 60}`
                  }}
                </td>
                <td class="border px-4 py-2">
                  {{ `${Math.floor(data.time / 60)}:${data.time % 60}` }}
                </td>
                <td class="border px-4 py-2">
                  {{ data.matchtime - data.time }}
                </td>
                <td class="border px-4 py-2">
                  {{ data.half }}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
//import videojs from "video.js";
import Plyr from "plyr";
import shaka from "shaka-player/dist/shaka-player.compiled.js";
import muxjs from "mux.js";

export default {
  name: "VideoPlayer",

  props: {
    videoOptions: {
      type: Object,
      default() {
        return {};
      },
    },
  },

  data() {
    return {
      displayTime: "00:00:00",
      playState: true,
      isFullscreen: false,
      progress: "height: 10px;width: 0%",
      ActiveWebVTT: false,
      calbrateData: [],
      ActiveCalibrate: false,
      bcData: {},
      urlprefix: "ffa",
      video: null,
      webvtt: "",
      matchOffsetData: null,
      canvas: null,
      ctx: null,
      keyEvents: [],
      title: null,
      matchInfo: {},
      matchId: null,
      currentTime: 0,
      matchData: {},
      player: null,
      options: {
        autoplay: false,
        controls: false,
      },
      playerKey:
        "BCpkADawqM2QQRAICfpf23j4hDi7g-UlKZp6rNqo3jReJFEfkuhnjOfjJTUOryo27EoXjggPUBbgzBpPvNY7p-YM4Me1Sx3Khpn-FkMQqP5x66s_ULssIqU3V6C2s7odORMP7L_iAyuGwexk",
    };
  },
  async mounted() {
    let urlParams = new URLSearchParams(window.location.search);
    //For Audio manipulation

    window.muxjs = muxjs;

    this.canvas = document.getElementById("canvas");
    this.video = document.getElementById("video");
    this.ctx = this.canvas.getContext("2d");
    this.bcData = await this.getBC(urlParams.get("videoId"));
    this.urlprefix = urlParams.get("platform") == "stg" ? "ffastg" : "ffa";
    console.log(urlParams.get("platform"));
    this.title = this.bcData.name;
    this.matchId = this.bcData.custom_fields.matchid;

    this.getMatchData().then((res) => {
      this.keyEvents = res;
    });

    this.createwebvtt();

    if (this.bcData.sources.length > 1) {
      this.options.sources = this.bcData.sources.filter((v) => {
        return v.src.startsWith("https://");
      });
    } else {
      this.options.sources = this.bcData.sources;
    }
    this.initApp();
  },
  beforeDestroy() {},
  methods: {
    onErrorEvent(event) {
      // Extract the shaka.util.Error object from the event.
      this.onError(event.detail);
    },

    onError(error) {
      // Log the error.
      console.error("Error code", error.code, "object", error);
    },
    async initPlayer() {
      // Create a Player instance.
      const video = document.getElementById("video");
      const player1 = new Plyr(video);
      window.player = player1;

      const player = new shaka.Player(video);
      this.player = player1;

      // Listen for error events.
      player.addEventListener("error", this.onErrorEvent);

      // Try to load a manifest.
      // This is an asynchronous process.
      try {
        console.log(this.options.sources[0].src);
        await player.load(this.options.sources[0].src);

        await player.addTextTrack(
          `https://0l60wuyvp6.execute-api.ap-southeast-2.amazonaws.com/${this.urlprefix}/${this.matchId}.webvtt`,
          "en",
          "chapters",
          "text/vtt"
        );

        // This runs if the asynchronous load is successful.
        console.log("The video has now been loaded!");
      } catch (e) {
        // onError is executed if the asynchronous load fails.
        this.onError(e);
      }
    },
    initApp() {
      // Install built-in polyfills to patch browser incompatibilities.
      shaka.polyfill.installAll();

      // Check to see if the browser supports the basic APIs Shaka needs.
      if (shaka.Player.isBrowserSupported()) {
        // Everything looks good!
        this.initPlayer();
      } else {
        // This browser does not have the minimum set of APIs we need.
        console.error("Browser not supported!");
      }
    },

    async calibrate() {
      this.calbrateData = [];
      for (let i = 1; i < Math.ceil(this.player.duration / 300); i++) {
        var time = i * 300;
        this.player.currentTime = time;
        this.player.pause();
        let result = await this.readText();

        const regex = /^[\d+]*:[\d+]*$/gm;
        let resultText = result.TextDetections.filter((i) =>
          regex.test(i.DetectedText)
        );
        let matchtime =
          resultText[0] &&
          Number(resultText[0].DetectedText.split(":")[0]) * 60 +
            Number(resultText[0].DetectedText.split(":")[1]);
        let half;
        if (matchtime < 2700) {
          half = "FirstHalf";
        } else if (matchtime > 2700 && matchtime < 5400) {
          half = "SecondHalf";
        } else if (matchtime > 5400 && matchtime < 6300) {
          half = "ExtraFirstHalf";
        } else if (matchtime > 6300 && matchtime < 7200) {
          half = "ExtraSecondHalf";
        }
        resultText[0] &&
          this.calbrateData.push({
            time: result.time,
            matchtime,
            offset: result.time - matchtime,
            half,
          });
      }
      let matchOffsetData = {};
      matchOffsetData.FirstHalf = Math.abs(
        this.calbrateData.filter((r) => {
          if (r.half == "FirstHalf") return r;
        })[0].offset
      );
      matchOffsetData.SecondHalf = Math.abs(
        this.calbrateData.filter((r) => {
          if (r.half == "SecondHalf") return r;
        })[0].offset
      );
      if (
        this.calbrateData.filter((r) => {
          if (r.half == "ExtraFirstHalf") return r;
        }).length > 0
      ) {
        matchOffsetData.ExtraFirstHalf = Math.abs(
          this.calbrateData.filter((r) => {
            if (r.half == "ExtraFirstHalf") return r;
          })[0].offset
        );
        if (
          this.calbrateData.filter((r) => {
            if (r.half == "ExtraSecondHalf") return r;
          }).length > 0
        ) {
          matchOffsetData.ExtraSecondHalf = Math.abs(
            this.calbrateData.filter((r) => {
              if (r.half == "ExtraSecondHalf") return r;
            })[0].offset
          );
        }
      }
      this.matchOffsetData = matchOffsetData;
      this.keyEvents = await this.getMatchData();
      console.log(matchOffsetData);
    },
    onTimeUpdate() {
      this.currentTime = this.player.currentTime;
      this.captureVideo();
    },
    captureVideo() {
      return new Promise((resolve, reject) => {
        var vRatio =
          (this.canvas.height / this.video.videoHeight) * this.video.videoWidth;
        resolve(
          this.ctx.drawImage(this.video, 0, 0, vRatio, this.canvas.height)
        );
        this.canvas.onerror = reject;
      });
    },

    readText() {
      // this.ctx.drawImage(this.video, 0, 0);
      var myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");

      var raw = JSON.stringify({
        image: this.canvas
          .toDataURL("img/png")
          .split("data:image/png;base64,")[1],
        time: this.currentTime,
      });

      var requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: raw,
        redirect: "follow",
      };
      return new Promise((resolve, reject) => {
        fetch(
          "https://byfset8uye.execute-api.ap-southeast-2.amazonaws.com/default/FFAScoreReader",
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    async onLoadedMetadata(player) {
      console.log("player loaded Data!", player);
      this.canvas.width = 640;
      this.canvas.height = 360;
    },
    createwebvtt() {
      this.getWebvtt().then((r) => {
        this.webvtt = r;
      });
    },
    convertSecondstoTime(given_seconds) {
      let hours = Math.floor(given_seconds / 3600);
      let minutes = Math.floor((given_seconds - hours * 3600) / 60);
      let seconds = given_seconds - hours * 3600 - minutes * 60;

      let string =
        hours.toString().padStart(2, "0") +
        ":" +
        minutes.toString().padStart(2, "0") +
        ":" +
        seconds.toString().padStart(2, "0") +
        ".000";
      return string;
    },
    extractMatchInfo() {},
    getWebvtt() {
      return new Promise((resolve, reject) => {
        var requestOptions = {
          method: "GET",
          redirect: "follow",
        };

        fetch(
          `https://0l60wuyvp6.execute-api.ap-southeast-2.amazonaws.com/${this.urlprefix}/m${this.matchId}.webvtt`,
          requestOptions
        )
          .then((response) => response.text())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    getMatchData() {
      return new Promise((resolve, reject) => {
        var requestOptions = {
          method: "GET",
          redirect: "follow",
        };

        fetch(
          `https://0l60wuyvp6.execute-api.ap-southeast-2.amazonaws.com/${this.urlprefix}/m${this.matchId}.json`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
    getBC(videoId) {
      return new Promise((resolve, reject) => {
        var myHeaders = new Headers();
        myHeaders.append(
          "Accept",
          `application/json;pk=BCpkADawqM2QQRAICfpf23j4hDi7g-UlKZp6rNqo3jReJFEfkuhnjOfjJTUOryo27EoXjggPUBbgzBpPvNY7p-YM4Me1Sx3Khpn-FkMQqP5x66s_ULssIqU3V6C2s7odORMP7L_iAyuGwexk`
        );

        var requestOptions = {
          method: "GET",
          headers: myHeaders,
          redirect: "follow",
        };

        fetch(
          `https://edge.api.brightcove.com/playback/v1/accounts/6058022062001/videos/${videoId}`,
          requestOptions
        )
          .then((response) => response.json())
          .then((result) => resolve(result))
          .catch((error) => reject(error));
      });
    },
  },
};
</script>

<style scoped lang="postcss">
.video {
  /* override other styles to make responsive */
  width: inherit !important;
  height: inherit !important;
}
.moment {
  @apply border text-xs text-center;
}
.overlay {
  position: absolute;
  width: 100%;
  fill: black;
  height: 100%;
  z-index: 1;
  bottom: 0;
